import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';

// environment
import { environment } from 'src/environments/environment';

// plugin
// import swal from 'sweetalert2';

@Injectable({
  providedIn: 'root',
})

export class CommonService {
  public apiUrl = '';
  public router;
  // public swal = swal;
  constructor(public http: HttpClient, public route: Router) {
    this.apiUrl = environment.apiURL;
    this.router = route;
  }

  /**********************************************************
  @PURPOSE : getToken.
  /********************************************************/
  public getToken(key) {
    return window.localStorage.getItem(key);
  }

  /**********************************************************
  @PURPOSE :setToken.
  /********************************************************/
  public setToken(key, value) {
    window.localStorage.setItem(key, value);

  }

  /**********************************************************
  @PURPOSE : Call api.
  /********************************************************/
  callApi(url, data, method): Promise<any> {
    return new Promise((resolve, reject) => {
      if (method === 'post') {
        this.http.post(this.apiUrl + url, data).subscribe(data1 => {
          resolve(data1);
        }, error => {
        });
      } else if (method === 'get') {
        this.http.get(this.apiUrl + url, { params: data })
          .subscribe(data1 => {
            resolve(data1);
          }, error => {
          });
      } else if (method === 'put') {
        this.http.put(this.apiUrl + url, data)
          .subscribe(data1 => {
            resolve(data1);
          }, error => {
          });
      } else if (method === 'delete') {
        this.http.delete(this.apiUrl + url)
          .subscribe(data1 => {
            resolve(data1);
          }, error => {
          });
      }
    });

  }
  /****************************************
  @purpose : popToaster
  /***************************************/
  // popToast(type, title) {
  //   swal({
  //     position: 'center',
  //     type,
  //     text: title,
  //     showConfirmButton: false,
  //     timer: 2000,
  //     customClass: 'custom-toaster',
  //   });
  // }

  /****************************************
  @purpose : AllowAlphaNumeric
  /***************************************/
  AllowAlphaNumeric(e) {
    let input;
    if (e.metaKey || e.ctrlKey) {
      return true;
    }
    if (e.which === 32) {
      return false;
    }
    if (e.which === 0) {
      return true;
    }
    if (e.which < 33) {
      return true;
    }
    if (e.which === 43 || e.which === 45) {
      return true;
    }
    if ((e.keyCode > 64 && e.keyCode < 91) || (e.keyCode > 96 && e.keyCode < 123) || e.keyCode === 8) {
      return true;
    }

    input = String.fromCharCode(e.which);
    return !!/[\d\s]/.test(input);
  }


}
