import { Component, OnInit } from '@angular/core';

// Service
import { CommonService } from '../common/common.service';
import { SpinnerService } from '../common/spinner.service';


@Component({
  selector: 'app-spacex-program',
  templateUrl: './spacex-program.component.html',
  styleUrls: ['./spacex-program.component.css']
})
export class SpacexProgramComponent implements OnInit {

public limit = 100;
public spaceLaunuchList: any = [];
public launchYear: any = [
  { year:2006, selected: true },
  { year:2007, selected: false },
  { year:2008, selected: false },
  { year:2009, selected: false },
  { year:2010, selected: false },
  { year:2011, selected: false },
  { year:2012, selected: false },
  { year:2013, selected: false },
  { year:2014, selected: false },
  { year:2015, selected: false },
  { year:2016, selected: false },
  { year:2017, selected: false },
  { year:2018, selected: false },
  { year:2019, selected: false },
  { year:2020, selected: false }
];

public successLaunch: any = [
  {value: true, selected: false},
  {value: false, selected: false}
];
public successLand: any =  [
  {value: true, selected: false},
   {value: false, selected: false}
  ];

  constructor(public commonService: CommonService,
    public spinnerSer: SpinnerService) { }

  ngOnInit() {
    this.commonService.callApi('launches?' + 'limit=100' +  'launch_year=2006', '', 'get').then((success) => {
        this.spaceLaunuchList = success;
    });
  }

  successfulLand(sland) {
    this.commonService.callApi('launches?' + 'limit=10&' + 'land_success=' + sland, '', 'get').then((success) => {
      this.spaceLaunuchList = success;
  });
  }

  successfulLaunch(slaunch) {
    this.commonService.callApi('launches?' + 'limit=100&' + 'launch_success=' + slaunch, '', 'get').then((success) => {
      this.spaceLaunuchList = success;
  });
  }

  selectedLaunchYear(year) {
    this.launchYear.map((e) => {
     e.selected = e.year === year ?  true : false;
    })
    this.commonService.callApi('launches?' + 'limt=100&' + 'launch_year=' + year, '', 'get').then((success) => {
      this.spaceLaunuchList = success;
  });
  }

}
